.outter_containerWork {
    background: #f6f6f6;
    padding: 5em 4em;
    .motto_main {
        width: 100%;
        height: 800px;
        margin-top: 4em;
        @media screen and (max-width: 1023px) {
            height: 1500px;
        }
        .motto_grid {
            display: grid;
            grid-template-columns: 50% 50%;
            grid-template-rows: auto;
            grid-column-gap: 20px;
            height: auto;
            @media screen and (max-width: 1023px) {
                grid-template-columns: auto;
                height: 1500px;
            }
            .motto_left {
                height: 500px;
                h2 {
                    text-align: left;
                    letter-spacing: 0px;
                    color: #161616;
                    font-family: "Montserrat", sans-serif;
                    font-size: 40px;
                    line-height: 58px;
                    font-weight: 600;
                }
                .motto_item {
                    .motto_title {
                        h3 {
                            text-align: left;
                            letter-spacing: 0.3px;
                            color: #2c2c2c;
                            opacity: 1;
                            cursor: pointer;
                            margin-top: 2em;
                            font-family: "Montserrat", sans-serif;
                            font-size: 20px;
                            line-height: 32px;
                            font-weight: 600;
                        }
                        .line {
                            margin-top: 2em;
                            height: 2px;
                            width: 80% !important;
                            background-color: #bcbcbc;
                        }
                        .line.show {
                            display: none;
                        }
                    }
                    .motto_content {
                        overflow: hidden;
                        transition: all 0.5s cubic-bezier(0, 1, 0, 1);
                        max-height: 0;
                        p {
                            text-align: left;
                            letter-spacing: 0.24px;
                            color: #696969;
                            width: 80%;
                            font-family: "Montserrat", sans-serif;
                            font-size: 16px;
                            line-height: 28px;
                            font-weight: normal;
                        }
                    }
                    .motto_contentshow {
                        height: auto;
                        max-height: 999px;
                        transition: all 0.5s cubic-bezier(1, 1, 1, 0);
                        p {
                            text-align: left;
                            letter-spacing: 0.24px;
                            color: #696969;
                            opacity: 1;
                            width: 80%;
                            font-family: "Montserrat", sans-serif;
                            font-size: 16px;
                            line-height: 28px;
                            font-weight: normal;
                        }
                        div {
                            height: 2px;
                            width: 80% !important;
                            background-color: #bcbcbc;
                        }
                    }
                }
            }
            .motto_right {
                .stamp {
                    width: auto;
                    height: auto;
                    position: relative;
                    top: -700px;
                    right: -25px;
                    margin-left: 24em;
                    margin-bottom: 1em;
                }

                .motto_img {
                    img {
                        width: 500px;
                        height: 631px;
                    }
                }
            }
        }
    }
}
